import React, { useRef, useState } from "react";
import { CountdownCircleTimer } from 'react-countdown-circle-timer';
import {Button} from 'react-bootstrap';




function Timer() {

  const width = window.innerWidth;
  const height = window.innerHeight;
  const[lenth,setLenth] = useState(Math.min(width,height)-20)

  const[isReset,setIsReset] = useState(true);
  const[isStart,setIsStart] = useState(false);
  const[isPlaying,setIsPlaying] = useState(false);
  const[key,setKey] = useState(0);
  const[seconds,setSeconds] = useState(10);

  window.addEventListener("resize",()=>{setLenth(Math.min(width,height)-20)})

  //버튼
  const 시작정지 = () => {
    setIsPlaying(!isPlaying);
    setIsStart(!isStart)
  }

  const 초기화 = () => {
    setIsReset(!isReset);
    setKey(key + 1);
    setIsPlaying(false);
    setIsStart(false);
  }

  const 입력 = () => {
    setIsReset(!isReset);
    setKey(key + 1)
  }

  const ContainerStyle = {
    width: '100%',
    background: 'blue',
  }

  const WrapperStyle = {
    width: lenth,
    background: 'white',
    margin: '0 auto',
    
  }

  const BtnGroupStyle = {
    position: 'relative',
    height: '50px',
    width: lenth *1/3
    
    }

  const BtnStyle1 = {
    width: lenth * 1/9,
    margin: lenth * 1/100,
    fontSize: lenth * 1/50,
    padding: '10px 0',
    position: 'absolute',
    left: 0
  }

  const BtnStyle2 = {
    width: lenth * 1/9,
    margin: lenth * 1/100,
    fontSize: lenth * 1/50,
    padding: '10px 0',
    position: 'absolute',
    right: 0
  }

  const ClockStyle = {
    fontSize: '10em',
    color: 'red',
    transform: 'translateY(10%)'
  }
  



  return(
   
      
    <div style={ContainerStyle}>
      <div style={WrapperStyle}>
      <CountdownCircleTimer
        isPlaying={isPlaying}
        duration={parseInt(seconds)}
        key={key}
        size={lenth}
        colors={[
          ['#004777', 0.33],
          ['#F7B801', 0.33],
          ['#A30000', 0.33],
        ]}
        
        >
           {({remainingTime}) => {
             if(remainingTime===0){
               return <div>time's up</div>
             } else {
               return (
                 <div>
              {isReset
              ? <div>
                <input onChange={(e)=>{setSeconds(e.target.value)}} ></input>
                <Button variant="outline-danger" onClick={() => {입력()}}>입력</Button>
              </div>
              : <div style={ClockStyle}>
                {remainingTime}
                <div style={BtnGroupStyle}>
                {isStart
                ?<Button variant="outline-danger" style={BtnStyle1} onClick={()=>{시작정지()}}>정지</Button>
              :<Button variant="outline-danger" style={BtnStyle1} onClick={()=>{시작정지()}}>시작</Button>}
                <Button variant="outline-danger" style={BtnStyle2} onClick={()=>{초기화()}} >초기화</Button>
                </div>
                </div> }
              </div>
               )}
           }}
          
        </CountdownCircleTimer>
        <Button variant="outline-danger" onClick={()=>console.log("seconds:" + seconds + "key:" + {key})}>test</Button>
        </div>
        </div>

    
  
  )}
export default Timer;